@import url('https://fonts.googleapis.com/css?family=Lobster');
@import url('https://fonts.googleapis.com/css?family=Raleway:200');
.Background {
  background-image: url(bluepicture.png);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height:800px;
  -webkit-animation: fadein 2s ease-in;
          animation: fadein 2s ease-in;
}.Background:hover{
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.files {
  font-size: 18px;
  margin-top: 35px;
}
.filename{
  color: #2d526c;
  /* background-color: #7ac7f3; */
  padding: 5px;
  border-radius: 5px;
  text-decoration: none;
}.filename:hover{
  cursor: pointer;
  background-color: white;
}
.filename-small{
  color: #2d526c;
  /* background-color: #7ac7f3; */
  padding: 1px;
  border-radius: 5px;
  text-decoration: none;
  font-size: 15px;
}.filename-small:hover{
  cursor: pointer;
  background-color: white;
}
.dot {
  font-size: 20px;
  /* margin-bottom: 10px; */
  margin-right: 10px;
  margin-left: 10px;
}
.dot-small {
  font-size: 20px;
  /* margin-bottom: 10px; */
  margin-right: 3px;
  margin-left: 3px;
}
.Background-outside{
  background-image: url(bluepic.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: fixed;
  top:0px;
  bottom:0px;
  width: 1200px;
  overflow: hidden;
  overflow-x: hidden;
  overflow-y: hidden;
  left:1200px;
}
.Board{
  background-image: url(board.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: fixed;
  top: 0px;
  bottom: 0px;
  width: 140px;
  height: 140px;
  margin-top: -130px;
  margin-left: 40px;
}
.arrow{
  margin-bottom:30px;
}.arrow:hover{
  cursor: pointer;
  color: orange;
}
.about-me{
  width: 100%;
  /* height: 500px; */
  background-color: white;
  /* margin-top: 330px; */
  padding-top: 20px;
  position: absolute;
  top: 40px;
  bottom: 0;
  text-align: center;
  color: #ea4962;
  font-size: 25px;
  -webkit-animation-name: move;
          animation-name: move;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}
@-webkit-keyframes move {
    from {top: 700px}
    to {top:40px}
}
@keyframes move {
    from {top: 700px}
    to {top:40px}
}
.Experience{
  width: 100%;
  height: 700px;
  background-color: #8dccc4;
  padding-top: 50px;
  margin-top: 500px;
  font-size: 50px;
  font-weight: 100;
  color: white;
  text-align: center;
}
.container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  grid-auto-rows: minmax(100px, auto);
  margin: 20px;
  margin-top: 50px;
}

.Ignitr{
  background-image: url(ignitrpic.png);
  background-repeat: no-repeat;
  background-size: cover;
  grid-column: 1;
  grid-row: 1;
  color: rgba(65, 70, 83, 0);
  /* width: 500px; */
  height: 400px;
  font-family: Raleway;
  vertical-align: center;
  vertical-align: middle;
  font-size: 30px;
  line-height: 350px;
}.Ignitr:hover{
  cursor: pointer;
  -webkit-box-shadow: inset 0 0 0 1000px rgba(65, 70, 83, 0.7);
          box-shadow: inset 0 0 0 1000px rgba(65, 70, 83, 0.7);
  color: white;
}
.VirtualCoin{
  background-image: url(cryptocoin.png);
  background-repeat: no-repeat;
  background-size: cover;
  grid-column: 3;
  grid-row: 1;
  color: rgba(65, 70, 83, 0);
  /* width: 500px; */
  height: 400px;
  font-family: Raleway;
  vertical-align: center;
  vertical-align: middle;
  font-size: 30px;
  line-height: 350px;
}
.label{
    color: white;
    margin-top: 250px;
    font-size: 28px;
    font-weight: 100;
}
.Task{
  color: rgba(65, 70, 83, 0);
  background-image: url(task.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-color:white;
  height: 400px;
  grid-column: 2;
  grid-row: 1;
  font-family: Raleway;
  vertical-align: center;
  vertical-align: middle;
  font-size: 30px;
  line-height: 350px;

}.Task:hover{
  cursor: pointer;
  -webkit-box-shadow: inset 0 0 0 1000px rgba(65, 70, 83, 0.7);
          box-shadow: inset 0 0 0 1000px rgba(65, 70, 83, 0.7);
  color: white;
}
.about{
  font-size: 50px;
  font-weight: 100;
  color: #ea4962;
  text-align: left;
  padding-left: 530px;
  background-color:white;
  padding-right:30px;
  min-width: 400px;
}
.about-small{
  font-size: 50px;
  font-weight: 100;
  color: #ea4962;
  text-align: left;
  padding-left: 380px;
  padding-right:30px;
  background-color:white;
  min-width: 330px;
}
.about-full{
  font-size: 40px;
  font-weight: 100;
  color: #ea4962;
  text-align: left;
  padding-left: 50px;
  background-color:white;
  padding-right:30px;
  min-width: 330px;
}
.topBar{
  width: 100%;
  height: 20%;
  margin-top: 0px;
}
.modal{
  width: 65%;
  /* height: 200px; */
  margin: auto;
  margin-top: 50px;
  /* border: 0.1px solid #0a304c; */
  font-family: Raleway;
  text-align: center;
  font-size: 50px;
  color: #0a304c;
  -webkit-animation: fadein 2s ease-in;
  animation: fadein 2s ease-in;
  padding: 40px;
  background-color: #ecfafccf;
  border-radius: 5px;
}
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}
@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}
.modal-small{
  width: 80%;
  /* height: 200px; */
  margin: auto;
  margin-top: 50px;
  /* border: 0.1px solid #0a304c; */
  font-family: Raleway;
  text-align: center;
  font-size: 40px;
  color: #0a304c;
  -webkit-animation: fadein 2s ease-in;
  animation: fadein 2s ease-in;
  padding: 40px;
  background-color: #ecfafccf;
  border-radius: 5px;
}
.font{
  color: #ea4962;
}
.border{
  width: 100%;
    height: 200px;
    background-color: white;
    margin-top: -40px;
    z-index: 0;
}
.photo-big{
  background-image: url(profile.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 350px;
  width: 350px;
  float:left;
  border-radius: 50%;
  margin-left: 100px;
  border: 4px solid #E91E63;
  -webkit-animation: fadein 3s ease-in;
          animation: fadein 3s ease-in;
}
.photo-small{
  background-image: url(profile.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  margin-top:40px;
  height: 250px;
  width: 250px;
  float:left;
  border-radius: 50%;
  margin-left: 70px;
  border: 4px solid #E91E63;
  -webkit-animation: fadein 3s ease-in;
          animation: fadein 3s ease-in;
}
.photo-full{
  background-image: url(profile.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  /* margin-top: -60px; */
  height: 200px;
  width: 200px;
  border-radius: 50%;
  /* margin-left: 70px; */
  border: 4px solid #E91E63;
  -webkit-animation: fadein 2s ease-in;
  animation: fadein 2s ease-in;
  margin: auto;
  margin-top: -20px;
  margin-bottom: 20px;
}
.paragraph{
  font-size: 20px;
  color: #191618;
  line-height: 1.7;
  margin-top: 10px;
}
.logo{
  width: 30px;
  height: 30px;
  margin-right: 15px;
  margin-top: 20px;
  -webkit-transition: all 0.1s ease-in-out;
  -o-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}.logo:hover{
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
  cursor: pointer;
}



